.ng-content {
  position: fixed;
  top: 0;
  width: 100%;
  height: 64px;
  z-index: 10;
  background: #ffffffcc;
  transition-delay: 0.3s;
  transition: background 0.36s;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.1);
}

.ng-content-white {
  background-color: white;
}

.ng-pc {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.ng-pc-choice-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1200px;
}

.ng-pc-choice-right-content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.ng-pc-choice {
  width: 100px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 64px;
  color: #666666;
  cursor: pointer;
  transition: 0.2s;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.ng-pc-choice:hover {
  color: #000;
}

.ng-choice-content {
  display: flex;
  flex-direction: row;
}

.np-choice-icon-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.ng-choice-icon {
  margin-left: 4px;
  width: 16px;
}

.github-content {
  width: 60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.github-middle-content {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.icon {
  width: 20px;
  height: 20px;
}

.ng-mobile {
  display: none;
  height: 100%;
}

.ng-mobile-text {
  padding: 0px 16px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.ng-mobile-Placeholder {
  width: 40px;
}

.ng-mobile-dropdown {
  height: 100vh;
  display: none;
  background-color: white;
}

.ng-mobile-dropdown-show {
  display: block;
}

.ng-mogile-dropdown-text-content {
  padding: 0px 45px;
}

.ng-mobile-dropdown-text {
  width: 100%;
  height: 44px;
  color: rgba(0, 0, 0, 0.79);
  font-size: 15px;
  line-height: 44px;
  font-weight: 500;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 910px) {
  .ng-pc {
    display: none;
  }
  .ng-mobile {
    display: block;
  }
}
@media screen and (min-width: 911px) and (max-width: 1199px) {
  .ng-pc-choice-content {
    width: 800px;
  }
}
@media screen and (min-width: 1200px) and (max-width: 1792px) {
  .ng-pc-choice-content {
    width: 1000px;
  }
}
@media screen and (min-width: 1793px) {
  .ng-pc-choice-content {
    width: 1200px;
  }
}
